<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process278"></a>

<p><br></p>
<p><span class="rvts17">章节介绍</span></p>
<p><img width="586" height="559" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1526.jpg"><span class="rvts39"></span>
<br><span class="rvts21">通过鲜血之路并来到烦恼大道后，到达最终的神圣殿堂，在这里，我们将遇到达利斯，卢西安，以及被达利斯复活的布拉克斯。混乱的局面摆在眼前，一场称神大战一触即发。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



